<template>
<v-container >
  <div class="font-weight-black d-flex justify-center text-h4 accountList"></div>
  <div class="searchandlistblock">
    <!-- ID -->
    <div v-if="searchandlistshow[0]" class="searchandlist">
      <v-text-field
        v-model = "searchfishid"
        append-icon="mdi-magnify"
        label="ID"
        hide-details
        class=" text-white"
        style="width: 200px;"
        bg-color="rgba(255, 255, 255, 0.15)">
      </v-text-field>
    </div>
    <!-- ID -->
    <!-- ID -->
    <div v-if = "searchandlistshow[1]" class="searchandlist">
      <v-text-field
        v-model = "searchelementid"
        append-icon="mdi-magnify"
        label="ID"
        hide-details
        class=" text-white"
        style="width: 200px;"
        bg-color="rgba(255, 255, 255, 0.15)">
      </v-text-field>
    </div>
    <!-- ID -->
    <!--  -->
    <div v-if="searchandlistshow[2]" class="elementlist">
      <div style="color: aliceblue;">
        <v-select 
          v-model="elementitem"
          :items='elements'
          shaped
        >
        </v-select>
      </div>
      <!--  -->
    </div>
    <!--  -->
    <div v-if="searchandlistshow[3]" class="elementlist">
      <div style="color: aliceblue;">
        <v-select 
          v-model="elementuseitem"
          :items='elements'
          shaped
        >
        </v-select>
      </div>
    <!--  -->
    </div>
    <!--  -->
    <div class="searchandlistautoend">
      <div style="color: aliceblue">
        <v-select 
          v-model="location"
          :items="['','','']"
          shaped
        >
        </v-select>
      </div>
    </div>
    <!--  -->
  </div>
  <!--  -->
  <div> 
    <Table v-if = 'Tableshow[0]' :columns="fishcolumns" :data="searchfishidfunction()" >
    </Table>
    <div v-if = "Tableshow[1]">
      <div class = "stitle">
        <div v-for = "(item,index) in element" :key="index">
          <div v-if="selectelementlist(item)">
            {{ item }}
            <Table :columns= "elementcolumns" :data="searchelementidfunction(index)"></Table>
          </div>
        </div>
      </div>
    </div>
    <div v-if="Tableshow[2]">
      <div v-for="(item,index) in element " :key = 'index'  style="display: flex; align-items: center;" >
        <div v-if="selectelementuselist(item)" class="stitle2" style=" width: 50%; flex: none; " >
          {{ item }}
          <Bar :data="chartDatalist[index]" :options="chartoption"/>
        </div>
        <div v-if="selectelementuselist(item)" class="stitle2" style=" width: 50%; flex: none; ">
          <p>:{{ maxfunction(index) }}</p>
          <p>:{{ minifunction(index) }}</p>
          <p>:{{ averagefunction(index) }}</p>
          <p>:{{ medianfunction(index) }}</p>
          <!-- {{ getChartData() }} -->
        </div>
      </div>
    </div>
  </div>
  <!--  -->
</v-container>
</template>
<script>
    import { Bar } from 'vue-chartjs'
    import { Chart as ChartJS, Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale } from 'chart.js'
    ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale)
    export default {
    name: 'testmaintenance',
    components:{ Bar },
    data()  {
    return {
      chartDatalist: require('../assets/test_data/maintain/chartlist'),
      chartOptions: {
        responsive: true,
        plugins: {
          legend: {
            display: true,
            position: 'top'
          },
          title: {
            display: true,
            text: 'Bar Chart Example'
          }
        },
        scales: {
          x: {
            beginAtZero: true
          },
          y: {
            beginAtZero: true
          }
        }
      },
      location:null,
      elementitem:null,
      elementuseitem:null,
      searchfishid:null,
      searchelementid:null ,
      searchandlistshow:[true,false,false,false],
      Tableshow:[true,false,false],
      element:[
        '','','LED','','','','','XM',
        'LORA','','','','L','', 'L',
        '','','SG90','POGO',''
      ],
      elements:[
        '','','','LED','','','','','XM',
        'LORA','','','','L','', 'L',
        '','','SG90','POGO',''
      ],
      fishcolumns: [
        { title: 'UID', key: 'id', fixed: 'left', width: 110 },
        { title: '', key: 'wireboard', width: 100 },
        { title: '', key: 'fisheyes', width: 100 },
        { title: 'LED', key: 'LED', width: 100 },
        { title: '', key: 'photointerrupter', width: 110 },
        { title: '', key: 'infraredsensor', width: 120 },
        { title: '', key: 'gravitymotor', width: 100 },
        { title: '', key: 'battery', width: 100 },
        { title: 'XM', key: 'XMmotor', width: 100 },
        { title: 'LORA', key: 'LORA', width: 110 },
        { title: '', key: 'wirelesscharge', width: 100 },
        { title: '', key: 'fishmouth', width: 100 },
        { title: '', key: 'fishhead', width: 100 },
        { title: 'L', key: 'firstjoint', width: 110 },
        { title: '', key: 'firstshell', width: 150 },
        { title: 'L', key: 'secondjoint', width: 110 },
        { title: '', key: 'secondshell', width: 150 },
        { title: '', key: 'rasberrypizero', width: 110 },
        { title: 'SG90', key: 'SG90', width: 100 },
        { title: 'POGO', key: 'POGO', width: 120 },
        { title: '', key: 'fishtail', fixed: 'right', width: 100 },
      ],
      fishlist: require('../assets/test_data/maintain/fishlist.js'),
      elementcolumns:[
        {title: '',key:'elementid'},
        {title: '',key:'fishid'},
        {title: '',key:'active'},
        {title: '',key:'active'}
      ],
      elementlist: require('../assets/test_data/maintain/elementlist.js')
    }
  },
  created(){
    this.location = ''
    this.elementitem = ''
    this.elementuseitem = ''
    this.sectionshow();
    this.searchandlistdisplay();
    this.searchfishidfunction();
    this.searchelementidfunction();
    this.selectelementlist();
    this.selectelementuselist();
    // this.maxfunction();
    // this.minifunction();
    // this.averagefunction();
    // this.medianfunction();
  },
  methods:{
    sectionshow(){
      if(this.location === ''){
        this.Tableshow = [true,false,false];
      }
      else if (this.location === ''){
        this.Tableshow = [false,true,false];
      }
      else if (this.location === ''){
        this.Tableshow = [false,false,true];
      }
    },
    searchandlistdisplay(){
      if(this.location === ''){
        this.searchandlistshow = [true,false,false,false];
      }
      else if (this.location === ''){
        this.searchandlistshow = [false,true,true,false];
      }
      else if (this.location === ''){
        this.searchandlistshow = [false,false,false,true];
      }
    },
    searchfishidfunction(){
      if(!this.searchfishid){
        return this.fishlist;
      }
      else{
        return this.fishlist.filter(
          item => {
            const itemId = item.id.toString(); 
            return itemId.includes(this.searchfishid.toLowerCase());
          }
        )
      }
    },
    searchelementidfunction(index){
      if(!this.searchelementid){
        return this.elementlist[index]
      }
      else{
        return this.elementlist[index].filter(
          item => {
            const itemId = item.elementid; 
            return itemId.includes(this.searchelementid.toUpperCase());
          }
        )
      }
    },
    selectelementlist(item){
      if(this.elementitem === ''){
        return true;
      }
      else if(this.elementitem === item){
        return true;
      }
    },
    selectelementuselist(item){
      if(this.elementuseitem === ''){
        return true;
      }
      else if(this.elementuseitem === item){
        return true;
      }
    },
    maxfunction(index){
      let array = this.chartDatalist[index].datasets[0].data;
      return Math.max(...array);
    },
    minifunction(index){
      let array = this.chartDatalist[index].datasets[0].data;
      return Math.min(...array);
    },
    averagefunction(index){
      let array = this.chartDatalist[index].datasets[0].data;
      let sum = 0;
      for(let i = 0;i < array.length;i++){
        sum += array[i];
      }
      return Math.round(sum/array.length);
    },
    medianfunction(index){
      let array = Array.from(this.chartDatalist[index].datasets[0].data);
      array.sort((a,b) => a-b)
      if(array.length % 2 == 1){
        return array[array.length/2]
      }
      else{
        return (array[array.length/2]+array[(array.length/2)-1])/2
      }
    },
  },
  watch:{
    location(){
      this.sectionshow();
      this.searchandlistdisplay();
    },
  },
}

</script>
<style scoped>
  .show{
    font-weight: bold;
    font-size: 30px;
    text-align: center;
  }
  .space{
    height: 50px;
    }
  .searchandlistblock{
    display:flex;
    justify-content: center;
  }
  .searchandlist{
    flex: none; 
    color: aliceblue;
  }
  .searchandlistautoend{
    display:flex;
    justify-content:flex-end;
    flex:auto;
    text-align : center;
  }
  .elementlist{
    flex: none; 
    color: aliceblue;
  }
  .stitle{
    color: aliceblue;
    font-size: 150%;
    font-weight: bold;
    margin-top: 1%;
    margin-bottom: 1%;
    text-align: center;
  }
  .stitle2{
    color: aliceblue;
    font-size: 150%;
    font-weight: bold;
    text-align: center; 
  }
  </style>